
@keyframes menu-present{
from{left:-15%
}
to{left:0
}
}
.docs-page{height:100%
}
.docs-page #demoComponent{height:400px
}
.docs-page>div{float:left;height:100%
}
.docs-page>div .back{font-size:14px;display:block;width:100%;position:absolute;left:0;top:0;background:#f3f3f3;padding:6px;box-sizing:border-box;text-align:center
}
.docs-page>div.nav{font-size:14px;font-family:sans-serif;padding-top:35px;padding-right:0;background-color:#f9f9f9;animation-name:menu-present;animation-duration:0.6s;animation-delay:0.4s;animation-timing-function:leaner;animation-fill-mode:forwards;position:fixed;left:-15%;top:0;width:15%;overflow:auto
}
.docs-page>div.nav .chapters{margin:10px 0 30px 35px
}
.docs-page>div.nav .chapters .chapter-title{border-bottom:1px #d2d2d2 solid;padding-bottom:15px;margin:10px 0;color:#716d6d
}
.docs-page>div.nav .anchor{display:block;padding:10px 0;cursor:pointer;transition:color .2s;border-right:2px solid transparent;white-space:nowrap;text-overflow:ellipsis;overflow:hidden;font-size:13px;color:#80a0b1
}
.docs-page>div.nav .anchor:hover{color:#4f8eca;font-weight:bold
}
.docs-page>div.nav .anchor.selected{color:#4f8eca;border-right-color:#4f8eca;font-weight:bold
}
.docs-page>div.main{width:80%;padding:30px 0 1rem 1rem;margin-left:15%
}
.docs-page>div.main .inner{padding:0 40px
}
.docs-page>div.main table{border-collapse:collapse;border-spacing:0;font-size:14px;width:100%
}
.docs-page>div.main table th,.docs-page>div.main table td{border:1px #efefef solid;padding:10px;font-weight:normal;line-height:24px
}
.docs-page>div.main table th{background:#f7f6f6
}
.docs-page>div.main table th:nth-child(1){width:15%
}
.docs-page>div.main table th:nth-child(2){width:15%
}
.docs-page>div.main table th:nth-child(3){width:70%
}
.docs-page>div.main a{margin:0 3px
}
.docs-page>div.main h1{font-size:26px
}
.docs-page>div.main h2{font-size:20px;margin:40px 0 20px 0
}
.docs-page>div.main h3{font-size:18px
}
.docs-page>div.main h4{font-size:16px
}
.docs-page>div.main hr{border:0;border-bottom:3px solid #e4e1e1;margin:35px 0
}
.docs-page>div.main p{font-size:14px;color:#666;line-height:30px
}
.docs-page>div a{text-decoration:none;color:#5194bf
}



